
import { defineComponent } from "vue";

import BrandTable from "@/components/BrandTable.vue";

import heartIcon from "@/assets/images/icons/png/heart.png";
import heartSolidIcon from "@/assets/images/icons/png/heart_solid.png";
import { useRouter } from "vue-router";

const Component = defineComponent({
  name: "MyFavorites",
  components: {
    BrandTable
  },
  props: {
    data: {
      type: Object,
      default: () => ({})
    }
  },
  setup() {
    const router = useRouter();

    const onCardClick = () => {
      router.push({ name: "BrandSummary", params: { id: "id" } });
    };
    const onFavoriteClick = () => {
      alert("favorite clicked");
    };
    const onCloseClick = () => {
      alert("close clicked");
    };

    return {
      heartIcon,
      heartSolidIcon,

      onCardClick,
      onFavoriteClick,
      onCloseClick
    };
  }
});

export default Component;
