const Component = defineComponent({
  name: "BrandTable",
  props: {
    maxHeight: {
      type: String,
      default: "500px"
    }
  },
  setup() {
    const router = useRouter();

    const onFavoriteClick = (row: any) => {
      console.log("row", row);
      alert(`favorite ${row.a} clicked`);
    };
    const onContactClick = () => {
      router.push({ name: "BrandInsights" });
    };

    const getPercentageClass = (value: number) => {
      return value >= 0 ? "positive" : "negative";
    };

    const getPercentageText = (value: number) => {
      const isPositive = value >= 0;
      return `${isPositive ? "+" : ""}${value}%`;
    };

    return {
      tableData,
      heartIcon,
      heartSolidIcon,
      uploadIcon,

      getPercentageClass,
      getPercentageText,
      onFavoriteClick,
      onContactClick
    };
  }
});

export default Component;
